import { CalendarSignalComponent } from './calendar-signal/calendar-signal.component';
import { EconomicCalendarCoreComponent } from './economic-calendar-core/economic-calendar-core.component';
import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';



const routes: Routes = [
  {
    path: '',
    component: EconomicCalendarCoreComponent,
    children: [
      {
        path: '',
        pathMatch: 'full',
        redirectTo: 'calendar-signal'
      },
      {
        path: 'calendar-signal',
        component: CalendarSignalComponent,
        data: {title: 'Calendar Signal', breadcrumb: 'calendar signal'}
      },
    ]
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class EconomicCalendarRoutingModule { }
