import { Component, OnInit } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { DatePipe } from '@angular/common';
import {  NgxSpinnerService } from 'ngx-spinner';

@Component({
  selector: 'app-calendar-signal',
  templateUrl: './calendar-signal.component.html',
  styleUrls: ['./calendar-signal.component.scss']
})
export class CalendarSignalComponent implements OnInit {

  calendars: any[] = [];
  constructor(
    private http: HttpClient ,
    private datepipe: DatePipe,
    private spinner: NgxSpinnerService) {

    }

  getCalendarByDate(date) {
    const selecteddate = this.datepipe.transform(date , 'yyyy-MM-dd');
    this.spinner.show();
    // tslint:disable-next-line: max-line-length
    this.http.get('http://5.189.170.190:3000/trading/economy-calendar/ByDate?DayRange=' + selecteddate).subscribe((signal: any) => {

       this.calendars = [].concat(...signal.data);
       this.spinner.hide();
       console.log(this.calendars);

    }, err => {
      this.spinner.hide();
    });
  }

  getCalendarByToday() {

    const todayDate = this.datepipe.transform(new Date() , 'yyyy-MM-dd');
    this.spinner.show();
    // tslint:disable-next-line: max-line-length
    this.http.get('http://5.189.170.190:3000/trading/economy-calendar/ByDate?DayRange=' + todayDate).subscribe((signal: any) => {

       this.calendars = [].concat(...signal.data);
       this.spinner.hide();
       console.log(this.calendars);

    }, err => {
      this.spinner.hide();
    });

  }

  getCalendarByDay(e) {

    this.spinner.show();
    this.http.get('http://5.189.170.190:3000/trading/economy-calendar?DateRange=' + e).subscribe((signal: any) => {

       this.calendars = [].concat(...signal.data);
       this.spinner.hide();
       console.log(this.calendars);

    }, err => {
      this.spinner.hide();
    });

  }

  ngOnInit(): void {

    this.getCalendarByToday();
  }


}
